import { Component, OnInit, Inject } from '@angular/core';
import { BotsComponent } from '../../bots/bots.component';
import { NgForm } from '@angular/forms';
import { Bot } from 'src/app/shared/models/bots.model';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-bot-copy',
  templateUrl: './bot-copy.component.html',
  styleUrls: ['./bot-copy.component.css']
})
export class BotCopyComponent implements OnInit {

  langs = [{code: 'en', name:'Ting Anh'},{code: 'vi', name:'Ting Vit'} ];

  model = new Bot(null, null, null,null, this.langs[0].code,null, null,null, true);
  botId: any;

  constructor(
    //private botServer: BotService,
    //private toastr: ToastrService,
    public dialogRef: MatDialogRef<BotsComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    ) {

      //console.log(data)
      this.botId = data.botId;
     }

  ngOnInit() {
  }

  newBot(f:NgForm) {

    let data = f.value;
    data.botId = this.botId
    this.dialogRef.close({action: 1, data: data});
  }

}
