import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ConfirmationDialogComponent } from 'src/app/bot/dialog/confirmation-dialog/confirmation-dialog.component';
import { UserService } from 'src/app/shared/services/user.service';

@Component({
  selector: 'app-managers',
  templateUrl: './managers.component.html',
  styleUrls: ['./managers.component.css']
})
export class ManagersComponent implements OnInit {

  //variable

  isLoading = true;
  users: any;
  userName: any;
  pass: any;


  page = 1;
  pageSize = 15;
  collectionSize: any;


  get userss(): any {
    return this.users
      .map((user, i) => ({ count: i + 1, ...user }))
      .slice((this.page - 1) * this.pageSize, (this.page - 1) * this.pageSize + this.pageSize);
  }


  constructor(
    private _snackBar: MatSnackBar,
    private dialog: MatDialog,
    private userService: UserService,
  ) {
  }


  ngOnInit() {
    this.getUser();
  }


  getUser = () => {
    this.userService.getAllUser()
      .subscribe(res => {

        this.users = res['data'];
        this.collectionSize = this.users.length;
        setTimeout(() => {
          this.isLoading = false;
        }, 500);

        //console.log(this.intents)
      },
        error => {
          this.isLoading = false;
        }

      );
  }


  //edit answer
  createUser() {

    let s = {
      userName: this.userName,
      pass: this.pass,
      role: 1,
      isAdmin : false,
    }
    this.userService.createUser(s).subscribe(
      res => {
        if (res.status === 200) {
          this.getUser();
          this._snackBar.open('Thao tc thnh cng', 'OK', {
            duration: 1000
          });
        } else {
          this._snackBar.open('Thao tc khng thnh cng', 'OK', {
            duration: 1000
          });
        }
      },
      error => {
        this._snackBar.open('Thao tc khng thnh cng', 'OK', {
          duration: 1000
        });
      }
    );
  }


  removeUser(item: any) {
    const dialogRef = this.dialog.open(ConfirmationDialogComponent, {
      data: "Bn c thc s mun xa thng tin ny khng?",
      width: '400px',
    });
    dialogRef.afterClosed().subscribe(result => {
      if (result) {
        this.userService.removeUserById(item._id)
          .subscribe(res => {
            if (res.status === 200) {
              this.getUser();
              this._snackBar.open('Thao tc thnh cng', 'OK', {
                duration: 1000
              });
            } else {
              this._snackBar.open('Thao tc khng thnh cng', 'OK', {
                duration: 1000
              });
            }
          }, error => {
            //this.getAll();
            this._snackBar.open('Thao tc khng thnh cng', 'OK', {
              duration: 1000
            });
            //this.connectFail = true;
          }
          );
      }
    });

  }
}