import { Component, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { MatSnackBar } from '@angular/material/snack-bar';
import { AuthService } from 'src/app/shared/services/auth.service';
import { UserService } from 'src/app/shared/services/user.service';

@Component({
  selector: 'app-profiles',
  templateUrl: './profiles.component.html',
  styleUrls: ['./profiles.component.css']
})
export class ProfilesComponent implements OnInit {
 
  changePassForm: FormGroup;

  isOverlay = false;
  uid: any;
  userName: any;
  numberTag: any;

  constructor(
    private user: UserService,
    private auth: AuthService,
    private _snackBar: MatSnackBar,
    private authService: AuthService,
  ) {

  }

  ngOnInit(): void {
    this.changePassForm = new FormGroup({
      'newPassword': new FormControl('', [Validators.required, Validators.minLength(6), Validators.maxLength(15)]),
      'oldPassword': new FormControl('', [Validators.required, Validators.minLength(6), Validators.maxLength(15)]),
      'rePassword': new FormControl('', [Validators.required, Validators.minLength(6), Validators.maxLength(15)]),
    });
    this.getCurrentProfile();
    this.getRole();
  }

  getCurrentProfile() {
    let data = this.auth.decoded();
    //console.log( data)
    if (data != null) {
      this.uid = data.uid;
    }
  }


  getRole() {
    let data = this.authService.decoded();
    this.userName = data.userName;
    this.numberTag = data.numberTag;
  }



  updatePassWord() {
    if (this.changePassForm.get('newPassword').value == this.changePassForm.get('rePassword').value) {
      let data = {
        id: this.uid,
        newPassword: this.changePassForm.get('newPassword').value,
        oldPassword: this.changePassForm.get('oldPassword').value
      }

      this.user.updatePass(this.uid,data).subscribe(res => {
        if (res != null) {
          this._snackBar.open('Thao tc thnh cng', 'OK', {
            duration: 1000
          });
          setTimeout(() => {
            this.auth.logOut();
          }, 1000)
        }
      }, error => {
        if (error.error) {
          this._snackBar.open('Thao tc khng thnh cng', 'OK', {
            duration: 1000
          });
        } else {
          this._snackBar.open('Thao tc khng thnh cng', 'OK', {
            duration: 1000
          });
        }
      });
    } else {
      confirm('Mt khu mi v nhp li mt khu khng khp. Vui lng kim tra li');
    }

  }

  refresh() {
    // do something
    this.getCurrentProfile();
  }


}