import { Component, OnInit } from '@angular/core';
import { BotCreateComponent } from '../dialog/bot-create/bot-create.component';
import { MatSnackBar } from '@angular/material/snack-bar';
import { BotService } from 'src/app/shared/services/bots.service';
import { BotRemoveComponent } from '../dialog/bot-remove/bot-remove.component';
import { Bot } from 'src/app/shared/models/bots.model';
import { MatDialog } from '@angular/material/dialog';
import { BotCopyComponent } from '../dialog/bot-copy/bot-copy.component';

@Component({
  selector: 'app-bots',
  templateUrl: './bots.component.html',
  styleUrls: ['./bots.component.css']
})
export class BotsComponent implements OnInit {

  loading = true;
  //variable
  bots: any = [];



  constructor(
    private botServer: BotService,
    public dialog: MatDialog,
    private _snackBar: MatSnackBar
    ) { }

  ngOnInit() {
    this.getAllBot();
  }

  // reload data bot
  refresh() {
    this.getAllBot();
  }

  // opne diaglog create bot
  diagCreateBot() {
    this.createBot();
  }
  // opne diaglog remove bot
  diagRemoveBot(data: any) {
    this.removeBot(data);
  }










  //get all bot
  getAllBot() {
    let currentUser = localStorage.getItem("currentUser");
    let userName  = JSON.parse(currentUser).userName.toLowerCase();
    this.botServer.getAll(userName, null)
      .subscribe(res => {

        //this.connectFail = false;
        this.bots = res['data'] as Bot[];
        localStorage.setItem('tpsoftbot_client', JSON.stringify(this.bots));
        setTimeout(() => {
          this.loading = false;
        }, 500);

      },
        error => {
          this.loading = false;
          this._snackBar.open(' c li xy ra', 'OK', {
            duration: 1000
          });
          //this.connectFail = true;
        }
      );
  }

  //save localstorage data info bot
  saveLocalStorageBot(data: any){
    localStorage.setItem('tpsoftbot_client', JSON.stringify(data));
  }

  //create bot
  createBot() {
    const dialogRef = this.dialog.open(BotCreateComponent, {
      data: null,
      width: '400px',
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result && result.action === 1) {
        let currentUser = localStorage.getItem("currentUser");
        result.data.owner = JSON.parse(currentUser).userName.toLowerCase();
        this.botServer.createBots(result.data).subscribe(
          res => {
            if (res.status === 200) {
              this.getAllBot();
              this._snackBar.open('Thao tc thnh cng', 'OK', {
                duration: 1000
              });
            } else {
              this._snackBar.open('Thao tc khng thnh cng', 'OK', {
                duration: 1000
              });
            }
          },
          error => {
            this._snackBar.open('Thao tc khng thnh cng', 'OK', {
              duration: 1000
            });
            //this.connectFail = true;
          }
        );
      }
    });

  }


    //copy bot
    copyBot(item: any) {
      const dialogRef = this.dialog.open(BotCopyComponent, {
        data: item,
        width: '400px',
      });
  
      dialogRef.afterClosed().subscribe(result => {
        if (result && result.action === 1) {
          let currentUser = localStorage.getItem("currentUser");
          result.data.owner = JSON.parse(currentUser).userName.toLowerCase();
          this.botServer.copyBots(result.data).subscribe(
            res => {
              if (res.status === 200) {
                this.getAllBot();
                this._snackBar.open('Thao tc thnh cng', 'OK', {
                  duration: 1000
                });
              } else {
                this._snackBar.open('Thao tc khng thnh cng', 'OK', {
                  duration: 1000
                });
              }
            },
            error => {
              this._snackBar.open('Thao tc khng thnh cng', 'OK', {
                duration: 1000
              });
              //this.connectFail = true;
            }
          );
        }
      });
  
    }

  //remove bot
  removeBot(bot:any) {
    const dialogRef = this.dialog.open(BotRemoveComponent, {
      data: bot,
      width: '400px',
    });

    dialogRef.afterClosed().subscribe(result => {
      if (result && result.action === 1) {
        this.botServer.removeBot(bot.botId).subscribe(
          res => {
            if (res.status === 200) {
              this.getAllBot();
              this._snackBar.open('Thao tc thnh cng', 'OK', {
                duration: 1000
              });
            } else {
              this._snackBar.open('Thao tc khng thnh cng', 'OK', {
                duration: 1000
              });
            }
          },
          error => {
            this._snackBar.open('Thao tc khng thnh cng', 'OK', {
              duration: 1000
            });
            //this.connectFail = true;
          }
        );
      }
    });
  }

  
  getRandomColor() {
    var length = 6;
    var chars = '0123456789ABCDEF';
    var hex = '#';
    while(length--) hex += chars[(Math.random() * 16) | 0];
    return hex;
  }

}
