import { Component, OnInit } from '@angular/core';
import { Event as RouterEvent, Router, NavigationStart, NavigationEnd, NavigationCancel, NavigationError } from '@angular/router';
import { AuthService } from '../shared/services/auth.service';

@Component({
  selector: 'app-customer',
  templateUrl: './customer.component.html',
  styleUrls: ['./customer.component.css']
})
export class CustomerComponent implements OnInit {

  screenWidth: number;
  loading = true;
  userName: any;
  numberTag: any;

  constructor(
    private router: Router,
    private authService: AuthService,
    ) {
    // set screenWidth on page load
    this.screenWidth = window.innerWidth;
    window.onresize = () => {
      // set screenWidth on screen size change
      this.screenWidth = window.innerWidth;
    };

    router.events.subscribe((event: RouterEvent) => {
      this.navigationInterceptor(event)
    })
  }

  ngOnInit(): void {
    this.getRole();
  }

  logout(){
    this.authService.logOut();
  }

  getRole() {
    let data = this.authService.decoded();
    this.userName = data.userName;
    this.numberTag = data.numberTag;
    //console.log(data)
  }

  gotoProfile(){
    let url = this.router.serializeUrl(
      this.router.createUrlTree([`/profiles`])
    );
  
    window.open(url, '_blank');
  }


  // Shows and hides the loading spinner during RouterEvent changes
  navigationInterceptor(event: RouterEvent): void {
    if (event instanceof NavigationStart) {
      this.loading = true
    }
    if (event instanceof NavigationEnd) {
      setTimeout(() => {
        this.loading = false;
      }, 500);
    }

    if (event instanceof NavigationCancel) {
      this.loading = false
    }
    if (event instanceof NavigationError) {
      this.loading = false
    }
  }

}
